import React, { useEffect,useState } from "react";
import api from '../../axios';
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles';
import YouTube from 'react-youtube';
import movieTrailer from 'movie-trailer';
import './category.css';
const image_base_URL = 'https://image.tmdb.org/t/p/original';

const useStyles = makeStyles((theme: Theme) =>
createStyles({
  title:{
        color    : 'white',
        fontFmily: 'Verdana, Arial, Helvetica, sans-serif',
        display  : 'flex',
        fontSize : '25px'
  },
Poster : {
     objectFit  : 'contain',
         width  : '100%',
     maxHeight  : '150px',
     transition :`transform 450ms`,
     marginRight:'10px',
     '&:hover' : {
         transform:'scale(1.08)'
     }
},

Posters : {
      display    : 'flex',
      overflowY  : 'hidden',
      overflowX  : 'scroll',
        padding  :  '20px',
      '&::-webkit-scrollbar': {
        display: 'none'
      }
},
largeSizePoster : {
       maxHeight:'250px'
},
popupBox: {
  position: 'fixed',
  background: '#00000050',
  width: '100%',
  height: '150vh',
  top: '0',
  left: '0',
},
box :{
  position: 'relative',
  width: '80%',
  margin: '0 auto',
  height: '80vh',
  maxHeight: '150vh',
  marginTop: 'calc(90vh - 85vh - 0px)',
  background: '#111',
  borderRadius: '20px',
  padding: '20px',
  border: '1px solid #999',
  overflow: 'auto'
},
closeicon: {
  content: 'x',
  cursor: 'pointer',
  position: 'fixed',
  right: 'calc(15% - 100px)',
  top: 'calc(100vh - 85vh - 90px)',
  background: '#ededed',
  width: '25px',
  height: '25px',
  borderRadius: '50%',
  lineHeight: '20px',
  textAlign: 'center',
  border: '1px solid #999',
  fontSize: '20px'
}
}));

 function Category(props) {
const classStyle = useStyles();
  const [movies,setMovies] = useState([]);
  const [trailer,setTrailer] = useState("");
  const [popup,setpopup] =useState(true);

  useEffect(() => {
        async function fetchMovies(){
          const response=await api.get(props.resourceURL);
          console.log("json>>",response.data.results);
          setMovies(response.data.results);
        }
        fetchMovies();
  }, [props.resourceURL])

   function getTrailerLink(movie){
     setpopup(true);
     if(trailer){
       setTrailer("");
     }
          movieTrailer(movie.name||"",{ tmdbId: movie.id }).then((trailerURL)=>{
           const params = new URLSearchParams(new URL(trailerURL).search);
           console.log("params>>",params.get("v"));
           setTrailer(params.get("v"));

         }).catch(error =>{

           console.log("error>>>>",error);
     });
  }
const opts = {
      height: '390',
      width: '100%',
      playerVars: {
        autoplay: 1,
      },
    };
    return(
      
       <div className="Category">
           <h1 className = {classStyle.title}>{props.title}</h1>
           <div className={classStyle.Posters}>
                  {
                  movies.map(movie=>(
                    <img className={`${classStyle.Poster} ${ props.realPoster && classStyle.largeSizePoster }`} 
                    key={movie.id} src={`${image_base_URL}${props.realPoster?movie.poster_path:movie.backdrop_path}`} 
                    onClick = {()=>getTrailerLink(movie) }alt = {movie.name}></img>
                  ))}
           </div>
           {(trailer && popup) &&
           <div className={classStyle.popupBox}>
            <div className={classStyle.box}>
              <span className={classStyle.closeicon} onClick={(e)=>setpopup(false)} >x</span>
              <YouTube className="test" videoId = { trailer } opts={opts}/>
            </div>
          </div>
           
           }
       </div>);
  }

export default Category;
