import React,{ useState ,useEffect} from 'react';
import {db} from '../firebase';
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles';
import  { alert } from "react-alert-confirm";
import "react-alert-confirm/dist/index.css";
import Loader from "react-loader-spinner";

const useStyles = makeStyles((theme: Theme) =>
createStyles({
planscreen:{

},
plan:{
    color:'#fff',
    textAlign:'left',
    display:'flex',
    justifyContent:'space-between',
    padding:'20px',
    opacity:'0.6',
    '&: hover':{
        opacity:'1'
    }
},
button:{
    padading:'10px 20px',
    fontSize:'1rem',
    color:'#fff',
    backgroundColor:'#e50914',
    border:'none',
    cursor:'pointer',
    borderRadius:'5px',
    height:'40px',

},
h5:{
   textAlign:'left',
   fontSize:'20px',
   fontWeight:'600',
   color:'#fff'
},
h6:{
   textAlign:'left',
   fontSize:'15px',
   fontWeight:'400',
   color:'#fff'
}

}));
export default function PlansScreen() {
    const [products,setProducts] = useState(null);
    const classStyle = useStyles();

    useEffect(()=>{
       db.collection('products').where('active','==',true)
       .get().then((querySnapshot)=>{
            const products={};
            querySnapshot.forEach(async productDocs=>{
                 products[productDocs.id]=productDocs.data();
            })
             setProducts(products);
       });
      
    },[]);
    
    function handleSubmit(e){
        e.preventDefault();
        alert({
                title: 'This feature is yet to Implement',
                lang: 'en'});
    }

    console.log(products);
    return (<>
          {products?
                <div className={classStyle.planscreen}>
                        {products && Object.entries(products).map(([productId,productData])=>{
                        
                        return (
                            <div className={classStyle.plan} key={productId}>
                                        <div className={classStyle.info}>
                                        <h5 className={classStyle.h5}>{productData.name}</h5>
                                        <h6 className={classStyle.h6} >{productData.description}</h6>
                                    </div>
                                    <button onClick={handleSubmit}className={classStyle.button}>Subscribe
                                    </button>
                                </div>
                             );
                        })}
                </div>
        :<Loader type="TailSpin" color="#00BFFF" height={80} width={80}  />}
       </>
    )
}
